import { useState, createContext } from "react";

const ProjectsDataContext = createContext();

const ProjectsDataProvider = (props) => {
  const [projectsData, setProjectsData] = useState([
    {
      id: 1,
      name: "AtomOneDarkTheme",
      img: "atomonedark.png",
      desc: "A wide linux theme made in oomox with the Atom One Dark color scheme.",
    },
    {
      id: 2,
      name: "MinimalBrowserHomepage",
      img: "min-browser-homepage.png",
      desc: "A minimal homepage to use with your web browser.",
    },
    {
      id: 3,
      name: "SlotMachine",
      img: "slotmachine.png",
      desc: "A dead simple slot machine thingy. Made in vanilla js.",
    },
    {
      id: 4,
      name: "Notes",
      img: "notes.png",
      desc: "First thing I made after learning js.",
    },
  ]);
  return (
    <ProjectsDataContext.Provider value={[projectsData, setProjectsData]}>
      {props.children}
    </ProjectsDataContext.Provider>
  );
};

export default ProjectsDataProvider;
export { ProjectsDataContext };
